import React from 'react';
import ReactTable from 'react-table';
import get from 'lodash/get';

import 'react-table/react-table.css'

export default ({ data, type }) => {
  const columns = {
    Users: [{
      id: 'name',
      Header: 'Name',
      accessor: user => `${get(user, 'profile.firstName', '')} ${get(user, 'profile.lastName', '')}`
    }, {
      id: 'email',
      Header: 'Email',
      accessor: user => `${get(user, 'emails.0.address', '')}`
    }, {
      id: 'owner',
      Header: 'Owner',
      accessor: user => `${get(user, 'owner.name', '')} (${user.owner.type})`
    }],
    Requests: [{
      Header: 'Code',
      accessor: 'code'
    }, {
      id: 'booker',
      Header: 'Booker',
      accessor: r => `${get(r, 'booker.profile.firstName', '')} ${get(r, 'booker.profile.lastName', '')}`
    }, {
      id: 'company',
      Header: 'Company',
      accessor: r => `${get(r, 'company.name', '')}`
    }, {
      id: 'landlord',
      Header: 'Landlord',
      accessor: r => `${get(r, 'landlord.name', '')}`
    }],
    Apartments: [{
      Header: 'Code',
      accessor: 'code'
    }, {
      id: 'address',
      Header: 'Address',
      accessor: a => `${get(a, 'address.formattedAddress', '')}`
    }, {
      id: 'owner',
      Header: 'Owner',
      accessor: a => `${get(a, 'owner.name', '')}`
    }, {
      id: 'price',
      Header: 'Price/Month',
      accessor: a => `${get(a, 'pricing.price', 0)} ${get(a, 'pricing.currency', '').toUpperCase()}`
    }]
  };
  return <div>
    <h4>{type}</h4>
    <ReactTable
    defaultPageSize={5}
    minRows={1}
    data={data}
    columns={columns[type]}
  />
  </div>
}
